import { Link } from "react-router-dom";
import { useState } from "react";
import Joi from "joi";
import passwordComplexity from "joi-password-complexity";
import TextField from "../../Components/Input/TextField";
import Select from "../../Components/Input/Select";
import RadioInput from "../../Components/Input/Radio";
import CheckBoxInput from "../../Components/Input/CheckBox";
import Button from "../../Components/Button";
import logo from "../../images/DWEAR_Logo.png";
import styles from "./styles.module.scss";

const months = [
    { name: "January", value: 1 },
    { name: "February", value: 2 },
    { name: "March", value: 3 },
    { name: "April", value: 4 },
    { name: "May", value: 5 },
    { name: "June", value: 6 },
    { name: "July", value: 7 },
    { name: "August", value: 8 },
    { name: "September", value: 9 },
    { name: "October", value: 10 },
    { name: "November", value: 11 },
    { name: "December", value: 12 }
];

const genders = ["male", "female", "non-binary"];

const SignUp = () => {
    const [data, setData] = useState({
        email: "",
        pwd: "",
        name: "",
        month: "",
        year: "",
        day: "",
        gender: ""
    });

    const [errors, setErrors] = useState({});

    const handleInputState = (name, value) => {
        setData(data => ({...data, [name]: value}));
    };

    const handleErrorState = (name, value) => {
        value === ""
        ? delete errors[name] 
        : setErrors(() => ({...errors, [name]: value}));
    };

    const schema = {
        email: Joi.string().email({tlds: false}).required().label("Email"),
        pwd: passwordComplexity().required().label("Password"),
        name: Joi.string().required().min(2).max(10).label("Name"),
    }

    const handleSubmit = async(e) => {
        e.preventDefault();
        if (Object.keys(errors).length === 0) console.log(data);
        else console.log("Please fill out properly");
    }

    return(
        <div className={styles.container}>
            <div className={styles.logo}>
                <img src={logo} alt="logo"/>
            </div>
            <h1 className={styles.heading}>Sign up for free to start</h1>
            <Button 
                label="Sign up with Facebook"
                style = {{background: "#1877f2", color: "white"}}
            />
            <p className={styles.or}>or</p>
            <form onSubmit={handleSubmit} className={styles.form_container}>
                <h2 className={styles.form_heading}>Sign up with your email address</h2>
                <div className={styles.input_container}>
                    <TextField
                        label="Your email"
                        placeholder="Enter your email"
                        name="email"
                        handleInputState={handleInputState}
                        schema={schema.email}
                        handleErrorState={handleErrorState}
                        value={data.email}
                        error={errors.email}
                        required={true}
                    />
                </div>
                <div className={styles.input_container}>
                    <TextField
                        label="Your password"
                        placeholder="Enter your password"
                        name="pwd"
                        handleInputState={handleInputState}
                        schema={schema.pwd}
                        handleErrorState={handleErrorState}
                        value={data.pwd}
                        error={errors.pwd}
                        type="password"
                        required={true}
                    />
                </div>
                <div className={styles.input_container}>
                    <TextField
                        label="Your name"
                        placeholder="Enter your name"
                        name="name"
                        handleInputState={handleInputState}
                        schema={schema.name}
                        handleErrorState={handleErrorState}
                        value={data.name}
                        error={errors.name}
                        required={true}
                    />
                </div>
                <div className={styles.birthdate_container}>
                    <p>Your date of birth</p>
                    <div className={styles.birthdate}>
                        <div className={styles.month}>
                            <Select 
                                name="month"
                                handleInputState={handleInputState}
                                label="Month"
                                placeholder="Month"
                                options={months}
                                value={data.month}
                                required={true}
                            />
                        </div>
                        <div className={styles.day}>
                            <TextField 
                                name="day"
                                handleInputState={handleInputState}
                                label="Day"
                                placeholder="DD"
                                value={data.day}
                                required={true}
                            />
                        </div>
                        <div className={styles.year}>
                            <TextField 
                                name="year"
                                handleInputState={handleInputState}
                                label="Year"
                                placeholder="YYYY"
                                value={data.year}
                                required={true}
                            />
                        </div>
                    </div>
                </div>
                <div className={styles.input_container}>
                    <RadioInput
                        label="Your gender"
                        name="gender"
                        handleInputState={handleInputState}
                        options={genders}
                        required={true}
                    />
                </div>
                <div className={styles.submit_btn_wrapper}>
                    <Button
                        label="Sign Up"
                        type="sumbit"
                    />
                </div>
                <p className={styles.condition} style={{fontSize: "1.6rem"}}>
                    Have an account? <Link to="/login">Log in</Link>
                </p>
            </form>
        </div>
    );
};

export default SignUp;